import Editor from '@monaco-editor/react';
import { Form } from 'antd';
import './test.css';

export function MonacoEditorComponent(props: any) {
    const { label, value, setValue, language, height } = props;

    const lang = language !== undefined ? language : "json";
    const hg = height !== undefined ? height : "50";
    function handleChangeData(val: any) {
        if (setValue !== undefined) {
            setValue(val);
        }
    }

    return (
        <Form.Item
            label={label}>
            <Editor
                height={`${hg}vh`}
                value={value}
                defaultLanguage={lang}
                onChange={handleChangeData} 
                theme="vs-dark"/>
        </Form.Item>
    )
}