import { Drawer, Menu } from "antd";
import { useCallback, useEffect, useState } from "react";
import { Link } from "react-router-dom";
import { HomeService } from "../../services/homeService";
import { isMobile } from 'react-device-detect';
import { MyAxiosV2 } from "../../helper/myaxios";

export default function LoadMenu(props: any) {
    const [menu, setMenu] = useState([]);
    const [isMobi, setIsMobi] = useState(false);

    const RenderMenu = useCallback(() => {
        if (isMobile) {
            setIsMobi(true);
        } else {
            setIsMobi(false);
        }

        HomeService.GetListMenu(isMobi)
            .then((rs: any) => {
                if (rs.isSuccess) {
                    let menuData = rs.data.map((data: any, index: number) => {
                        return {
                            key: index,
                            label: data.title,
                            children: data.menu.map((data1: any) => {
                                return {
                                    label: (
                                        <Link to={data1.route}>{data1.title}</Link>
                                    ),
                                    key: data1.route
                                }
                            })
                        };
                    });
                    setMenu(menuData);
                }
                else {
                    MyAxiosV2.SetAccessToken(null);
                    MyAxiosV2.SetRefreshToken(null);
                }
            })
    }, [setMenu, isMobi]);

    useEffect(() => {
        RenderMenu();
    }, [RenderMenu]);

    function onCloseMenu() {
        props.setVisible(false);
    }

    return (
        <div>
            <Drawer
                open={props.visible}
                placement="left"
                onClose={onCloseMenu}
                width={200}>
                <Menu items={menu} />
            </Drawer>
        </div>
    );
}